<template>
  <div class="hostservice-ips-config_handle">
    <mu-form
      :model="formData"
      class="mu-demo-form"
      :label-position="labelPosition"
      label-width="100"
      ref="hostservice_ips_config_handle"
    >
      <mu-form-item prop="handle" label="处理方式">
        <mu-radio
          v-model="formData.handle"
          v-for="item in dropDownData.handle"
          :key="item.id"
          :value="item.id"
          :label="item.name"
        ></mu-radio>
      </mu-form-item>
      <mu-form-item prop="is_status" label="是否启用">
        <mu-switch v-model="formData.is_status"></mu-switch>
      </mu-form-item>
      <mu-form-item>
        <mu-button color="primary" small @click="submitSave">确定</mu-button>
        <mu-button @click="cancel" small>取消</mu-button>
      </mu-form-item>
    </mu-form>
  </div>
</template>
<script>
import { setHandleOffice } from "req/api/hostservice/ips";
export default {
  name: "hostservice_ips_config_handle",
  data() {
    return {
      labelPosition: "right",
      formData: {
        host_id: "",
        id: "",
        handle: "",
        status: 0,
        is_status: false
      }
    };
  },
  props: {
    layer_data: {
      type: [Object, Array, Number],
      default: () => {
        return {};
      }
    },
    host_id: {
      type: Number
    },
    dropDownData: {
      type: Object,
      default: () => {
        return {};
      }
    },
    layerid: {
      type: String
    }
  },
  created() {
    this.__editInfo();
  },
  methods: {
    submitSave() {
      this.$refs.hostservice_ips_config_handle.validate().then(result => {
        if (result) {
          this.$isShowTip(
            "hostservice",
            "hostservice_ips",
            "确定保存吗?",
            () => {
              this.formData.status = this.formData.is_status ? 1 : 0;
              this.__setHandleOffice(this.formData);
            },
            this
          );
        }
      });
    },
    cancel() {
      this.$isShowTip(
        "hostservice",
        "hostservice_ips",
        "确定取消吗？",
        () => {
          this.$layer.close(this.layerid);
        },
        this
      );
    },
    __editInfo() {
      this.formData.host_id = this.host_id;
      this.formData.id = this.layer_data.id;
      this.formData.handle = this.layer_data.handle;
      this.formData.status = this.layer_data.status;
      this.formData.is_status = this.layer_data.status == 1 ? true : false;
    },
    __setHandleOffice(param) {
      setHandleOffice(param)
        .then(res => {
          this.$message({
            type: "success",
            message: "保存成功"
          }),
            this.$parent.reloadAll();
          this.$layer.close(this.layerid);
        })
        .catch(err => {});
    }
  }
};
</script>
<style scoped>
.hostservice-ips-config_handle {
  width: 100%;
  padding: 10px 20px 0 0;
}
</style>





