<template>
  <div class="hostservice-ips-custom_config_add">
    <mu-form
      :model="formData"
      class="mu-demo-form"
      :label-position="labelPosition"
      label-width="140"
      ref="hostservice_ips_custom_config_add"
    >
      <mu-form-item prop="name" label="规则名称" :rules="nameRules">
        <mu-text-field v-model="formData.name" :max-length="50"></mu-text-field>
      </mu-form-item>
      <mu-form-item
        prop="description"
        label="规则说明"
        :rules="descriptionRules"
      >
        <mu-text-field
          v-model="formData.description"
          :max-length="50"
        ></mu-text-field>
      </mu-form-item>
      <mu-form-item prop="type" label="规则类型" :rules="typeRules">
        <mu-select v-model="formData.type">
          <mu-option
            v-for="item in dropDownData.type"
            :key="item.id"
            :label="item.name"
            :value="item.id"
          ></mu-option>
        </mu-select>
      </mu-form-item>
      <mu-form-item
        prop="content"
        label="规则内容"
        :rules="contentRulesAll[formData.type]"
      >
        <mu-text-field
          v-model="formData.content"
          :max-length="50"
        ></mu-text-field>
      </mu-form-item>
      <mu-form-item prop="handle" label="处理方式">
        <mu-radio
          v-show="!(module_type == 2 && !(item.id == 2))"
          v-model="formData.handle"
          v-for="item in dropDownData.handle"
          :key="item.id"
          :value="item.id"
          :label="item.name"
        ></mu-radio>
      </mu-form-item>
      <mu-form-item prop="status" label="是否启用">
        <mu-radio
          v-model="formData.status"
          v-for="item in dropDownData.status"
          :key="item.id"
          :value="item.id"
          :label="item.name"
        ></mu-radio>
      </mu-form-item>
      <mu-form-item prop="intranet" label="是否监控内部网络">
        <mu-radio
          v-model="formData.intranet"
          v-for="item in dropDownData.intranet"
          :key="item.id"
          :value="item.id"
          :label="item.name"
        ></mu-radio>
      </mu-form-item>
      <mu-form-item>
        <mu-button color="primary" small @click="submitSave">确定</mu-button>
        <mu-button @click="cancel" small>取消</mu-button>
      </mu-form-item>
    </mu-form>
  </div>
</template>
<script>
import { addRuleCustom } from "req/api/hostservice/ips";
export default {
  name: "hostservice_ips_custom_config_add",
  data() {
    return {
      labelPosition: "right",
      nameRules: [{ validate: val => !!val, message: "规则名称不能为空" }],
      descriptionRules: [
        { validate: val => !!val, message: "规则说明不能为空" }
      ],
      typeRules: [{ validate: val => !!val, message: "请选择规则类型" }],
      contentRulesAll: {
        "9": [
          { validate: val => !!val, message: "规则内容不能为空" },
          {
            validate: val =>
              !isNaN(val) && parseInt(val) > 0 && parseInt(val) < 65536 && /^[^0]/.test(val),
            message: "请输入正确的端口号,端口号大于0小于65536"
          }
        ],
        "10": [
          { validate: val => !!val, message: "规则内容不能为空" },
          {
            validate: val => {
              let reg = /^([\w-]+\.)+[a-z0-9]+$/;
              return reg.test(val);
            },
            message: "请输入正确的域名,例如:www.baidu.com"
          }
        ],
        "11": [
          { validate: val => !!val, message: "规则内容不能为空" },
          {
            validate: val => this.$validates.ipReg.test(val),
            message: "请输入正确的IP地址,例如:1.1.1.1"
          }
        ],
        "12": [
          { validate: val => !!val, message: "规则内容不能为空" },
          {
            validate: val => {
              let reg = /^[a-z]+$/;
              return reg.test(val);
            },
            message: "请输入正确的请求类型,例如: post get "
          }
        ],
        "13": [
          { validate: val => !!val, message: "规则内容不能为空" },
          {
            validate: val => {
              return !isNaN(val);
            },
            message: "只能输入数字"
          }
        ],
        "14": [
          { validate: val => !!val, message: "规则内容不能为空" },
          {
            validate: val => {
              let reg = /^\.[a-zA-Z]+$/;
              return reg.test(val);
            },
            message: "请正确输入文件类型,例如: .zip"
          }
        ],
        "15": [
          { validate: val => !!val, message: "规则内容不能为空" },
          {
            validate: val => {
              let reg = /^(\/[^/]+)+\/$/;
              return reg.test(val);
            },
            message: "请正确输入目录结构,例如: /user/"
          }
        ],
        "16": [{ validate: val => !!val, message: "规则内容不能为空" }],
        "17": [{ validate: val => !!val, message: "规则内容不能为空" }]
      },
      formData: {
        host_id: this.host_id,
        name: "",
        type: "",
        content: "",
        description: "",
        handle: 2,
        status: 1,
        intranet: 1
      }
    };
  },
  props: {
    dropDownData: {
      type: Object,
      default: () => {
        return {};
      }
    },
    host_id: Number,
    layerid: {
      type: String
    },
    module_type: Number
  },
  methods: {
    submitSave() {
      this.$refs.hostservice_ips_custom_config_add.validate().then(result => {
        if (result) {
          this.$isShowTip(
            "hostservice",
            "hostservice_ips",
            "确定添加吗?",
            () => {
              this.__addRuleCustom(this.formData);
            },
            this
          );
        }
      });
    },
    cancel() {
      this.$isShowTip(
        "hostservice",
        "hostservice_ips",
        "确定取消吗？",
        () => {
          this.$layer.close(this.layerid);
        },
        this
      );
    },
    __addRuleCustom(param) {
      addRuleCustom(param)
        .then(res => {
          this.$message({
            type: "success",
            message: "添加成功"
          });
          this.$parent.reload();
          this.$layer.close(this.layerid);
        })
        .catch(err => {});
    }
  }
};
</script>
<style scoped>
.hostservice-ips-custom_config_add {
  width: 100%;
  padding: 10px 20px 0 0;
}
</style>



