<template>
  <div class="hostservice-ips-config_set">
    <mu-form
      :model="formData"
      class="mu-demo-form"
      :label-position="labelPosition"
      label-width="110"
      ref="hostservice_ips_config_set"
    >
      <mu-form-item prop="status" label="功能设置">
        <mu-switch
          v-model="formData.status"
          :label="formData.status ? '开启' : '关闭'"
        ></mu-switch>
      </mu-form-item>
      <template v-if="formData.status">
        <mu-form-item prop="type" label="类型">
          <mu-radio
            v-model="formData.type"
            key="1"
            value="1"
            label="IPS"
            uncheck-icon=":iconfont icon-IPSecVPN gender-uncheck"
            checked-icon=":iconfont icon-IPSecVPN gender-check-nan"
          ></mu-radio>
          <mu-radio
            v-model="formData.type"
            key="2"
            value="2"
            label="IDS"
            uncheck-icon=":iconfont icon-ids gender-uncheck"
            checked-icon=":iconfont icon-ids gender-check-nan"
          ></mu-radio>
        </mu-form-item>
          <mu-form-item
          v-if="formData.type == 2"
            prop="device_ip"
            :label="host_os == 1 ? 'IP地址': '网卡地址'"
            :rules="deviceNameRules"
          >
            <mu-select v-model="formData.device_ip">
              <mu-option
                v-for="item in dropDownData.network_name"
                :key="item"
                :label="item"
                :value="item"
              ></mu-option>
            </mu-select>
          </mu-form-item>
        <div v-if="formData.type == 1">
          <mu-form-item
            prop="firewall"
            label-width="210px"
            label="接管并锁定防火墙规则"
          >
            <mu-switch
              v-model="formData.firewall"
              :label="formData.firewall ? '是' : '否'"
            ></mu-switch>
          </mu-form-item>
          <div class="ips-notice">
            警告：接管并锁定防火墙规则后，不能在终端添加防火墙规则。
          </div>
          <mu-form-item prop="protect_ip" label="本机其他IP" :rules="ipsRules">
            <mu-text-field
              v-model="formData.protect_ip"
              :max-length="50"
            ></mu-text-field>
          </mu-form-item>
          <div class="ips-notice">
            备注：默认保护本机的所有网卡地址。
          </div>
        </div>
      </template>
      <mu-form-item>
        <mu-button color="primary" small @click="submitSave">确定</mu-button>
        <mu-button @click="cancel" small>取消</mu-button>
      </mu-form-item>
    </mu-form>
  </div>
</template>
<script>
import { getConfigData, setConfigData } from "req/api/hostservice/ips";

export default {
  name: "hostservice_ips_config_set",
  data() {
    return {
      labelPosition: "right",
      ipRules: [
        { validate: val => !!val, message: "IP地址不能为空" },
        {
          validate: val => this.$validates.ipReg.test(val),
          message: "IP格式不正确"
        }
      ],
      deviceNameRules: [
        { validate: val => !!val, message: "地址不能为空" }
      ],
      ipsRules: [
        {
          validate: val => this.$validates.checkMoreIp(val),
          message: "IP格式不正确(IP可为多个 如:1.1.1.1,1.1.6.9)"
        }
      ],
      formData: {
        host_id: this.host_id,
        black: false,
        type: "2",
        protect_ip: "",
        device_ip: "",
        firewall: true,
        status: true
      },
      dropDownData: {
        type: [
          {
            id: 1,
            name: "IPS"
          },
          {
            id: 2,
            name: "IDS"
          },
          {
            id: 0,
            name: "无"
          }
        ],
        black_list: [
          {
            id: 1,
            name: "开启"
          },
          {
            id: 0,
            name: "关闭"
          }
        ],
        firewall: [
          {
            id: 1,
            name: "是"
          },
          {
            id: 0,
            name: "否"
          }
        ],
        network_name:[]
      }
    };
  },
  props: {
    host_id: Number,
    layerid: String,
    host_os: Number,
    hostNameList:String
  },
  created() {
    this.setNetWork()
    this.__getConfigData({
      host_id: this.host_id
    });
  },
  methods: {
    setNetWork() {
      if(this.hostNameList) {
        this.dropDownData.network_name = this.hostNameList.split(',')
      }
    },
    submitSave() {
      this.$refs.hostservice_ips_config_set.validate().then(result => {
        if (result) {
          this.$isShowTip(
            "hostservice",
            "hostservice_ips",
            "确定保存吗?",
            () => {
              let data = JSON.parse(JSON.stringify(this.formData));
              data.firewall = data.firewall == true ? "1" : "0";
              data.black = data.black == true ? "1" : "0";
              data.status = data.status == true ? "1" : "0";
              this.__setConfigData(data);
            },
            this
          );
        }
      });
    },
    cancel() {
      this.$isShowTip(
        "hostservice",
        "hostservice_ips",
        "确定取消吗？",
        () => {
          this.$layer.close(this.layerid);
        },
        this
      );
    },
    __getConfigData(param) {
      getConfigData(param)
        .then(res => {
          this.formData.type = res.data.type.toString();
          this.formData.protect_ip = res.data.protect_ip;
          this.formData.device_ip = res.data.device_ip;
          this.formData.black = res.data.black == 1 ? true : false;
          this.formData.firewall = res.data.firewall == 1 ? true : false;
          this.formData.status = res.data.status == 1 ? true : false;
        })
        .catch(err => {});
    },
    __setConfigData(param) {
      setConfigData(param)
        .then(res => {
          this.$parent.reload();
          this.$message({
            type: "success",
            message: "操作已成功!正在下发命令至客户端,若命令未生效请检查通信日志!"
          });
          this.$layer.close(this.layerid);
        })
        .catch(err => {});
    }
  }
};
</script>
<style scoped lang="less">
.hostservice-ips-config_set {
  width: 100%;
  padding: 10px 20px 0 0;
  .ips-notice {
    padding-left: 50px;
    color: #f56c6c;
    position: relative;
    top: -14px;
  }
}
</style>



