<template>
  <div class="hostservice-ips-custom_strategy_unit">
    <g-el-table
      :tableHeadShow="tableHeadShow"
      :url="url"
      :type="type"
      :cloumns="cloumns"
      :params="params"
      :pageParams="pageParams"
      @selectChange="selectChange"
      ref="hostservice_ips_custom_strategy_unit_table"
    >
      <template v-slot:table_header="props">
        <el-button type="danger" size="mini" @click="del(selectData)">批量删除</el-button>
        <el-button type="primary" size="mini" @click="add">增加自定义规则</el-button>
        <el-button type="primary" size="mini" @click="clearAll">清空所有自定义规则</el-button>
        <el-button type="primary" size="mini" @click="reload">刷新</el-button>
      </template>
      <!-- 操作 -->
      <template v-slot:operation="props">
        <el-button
          type="primary"
          size="mini"
          v-if="props.data.row.status == 0"
          @click="openUsing(props.data.row)"
        >启用</el-button>
        <el-button
          type="danger"
          size="mini"
          v-else-if="props.data.row.status == 1"
          @click="stopUsing(props.data.row)"
        >停用</el-button>
      </template>
      <!-- 规则名称 -->
      <template v-slot:name_head="props">
        <el-input
          v-model="params.name"
          size="mini"
          placeholder="请输入内容"
          @keyup.enter.native="search"
          :maxLength="20"
        ></el-input>
      </template>
      <!-- 说明 -->
      <template v-slot:description_head="props">
        <el-input
          v-model="params.description"
          size="mini"
          placeholder="请输入内容"
          @keyup.enter.native="search"
          :maxLength="20"
        ></el-input>
      </template>
      <!-- 规则类型 -->
      <template v-slot:type_head="props">
        <el-select v-model="params.type" clearable @change="search" size="mini" placeholder="请选择">
          <el-option
            v-for="item in dropDownData.type"
            :key="item.id"
            :label="item.name"
            :value="item.id"
          ></el-option>
        </el-select>
      </template>
      <!-- 规则内容 -->
      <template v-slot:content_head="props">
        <el-input
          v-model="params.content"
          size="mini"
          placeholder="请输入内容"
          @keyup.enter.native="search"
          :maxLength="20"
        ></el-input>
      </template>
      <!-- 处理方式 -->
      <template v-slot:handle="props">
        <div v-if="props.data.row.handle == 2">
          <span v-html="icon.stateIcon.error"></span>警告
        </div>
        <div v-else-if="props.data.row.handle == 1">
          <span v-html="icon.stateIcon.success"></span>允许
        </div>
        <div v-else-if="props.data.row.handle == 0">
          <span v-html="icon.stateIcon.pending"></span>阻止
        </div>
      </template>
      <template v-slot:handle_head="props">
        <el-select v-model="params.handle" clearable @change="search" size="mini" placeholder="请选择">
          <el-option
            v-for="item in dropDownData.handle"
            :key="item.id"
            :label="item.name"
            :value="item.id"
          ></el-option>
        </el-select>
      </template>
      <!-- 是否启用 -->
      <template slot="status" slot-scope="props">
        <div v-if="props.data.row.status == 1">
          <span v-html="icon.stateIcon.success"></span>启用
        </div>
        <div v-else-if="props.data.row.status == 0">
          <span v-html="icon.stateIcon.error"></span>未启用
        </div>
      </template>
      <template v-slot:status_head="props">
        <el-select v-model="params.status" clearable @change="search" size="mini" placeholder="请选择">
          <el-option
            v-for="item in dropDownData.status"
            :key="item.id"
            :label="item.name"
            :value="item.id"
          ></el-option>
        </el-select>
      </template>
    </g-el-table>
  </div>
</template>
<script>
import configDetail from "./config_detail";
import customConfigAdd from "./add_custom_config";
import {
  clearRuleCustom,
  openUsedCustom,
  stopUsedCustom,
  delRuleCustom,
  getDropDownDataCustom
} from "req/api/hostservice/ips";
export default {
  data() {
    return {
      url: `/ipsrule/${this.host_id}/list`,
      type: "get",
      cloumns: [
        {
          type: "selection",
          fixed: "left"
        },
        {
          type: "index",
          // fixed: "left",
          label: "序号",
          second_head: {
            type: "index",
            width: "60px",
            label: " ",
            indexMethod: function(index) {
              return index + 1;
            }
          }
        },
        {
          label: "规则名称",
          sortable: true,
          prop: "name",
          second_head: {
            width: "150px",
            prop: "name",
            templateHeadName: "name_head"
          }
        },
        {
          label: "规则说明",
          sortable: true,
          prop: "description",
          second_head: {
            minWidth: "150px",
            prop: "description",
            templateHeadName: "description_head"
          }
        },
        {
          label: "规则类型",
          sortable: true,
          prop: "type",
          second_head: {
            width: "150px",
            prop: "type",
            templateHeadName: "type_head"
          }
        },
        {
          label: "规则内容",
          sortable: true,
          prop: "content",
          second_head: {
            minWidth: "150px",
            prop: "content",
            templateHeadName: "content_head"
          }
        },
        {
          label: "处理方式",
          sortable: true,
          prop: "handle",
          second_head: {
            isTemplate: true,
            templateName: "handle",
            width: "150px",
            prop: "handle",
            templateHeadName: "handle_head"
          }
        },
        {
          label: "是否启用",
          prop: "status",
          sortable: true,
          second_head: {
            isTemplate: true,
            templateName: "status",
            width: "150px",
            templateHeadName: "status_head",
            prop: "status"
          }
        },
        {
          label: "操作",
          second_head: {
            label: "",
            width: "100px",
            isTemplate: true,
            templateName: "operation"
          }
        }
      ],
      tableHeadShow: true,
      params: {
        page: 1,
        limit: 10,
        name: "",
        description: "",
        type: "",
        content: "",
        handle: "",
        status: ""
      },
      pageParams: {
        layout: "total, sizes, prev, pager, next, jumper",
        pageAlign: "left",
        pageSizes: [10, 20, 30]
      },
      dropDownData: {
        status: [
          {
            id: 1,
            name: "启用"
          },
          {
            id: 0,
            name: "未启用"
          }
        ],
        handle: [
          {
            id: 0,
            name: "阻止"
          },
          {
            id: 1,
            name: "允许"
          },
          {
            id: 2,
            name: "警告"
          }
        ],
        type: [],
        intranet: [
          {
            id: 1,
            name: "是"
          },
          {
            id: 0,
            name: "不是"
          }
        ]
      },
      selectData: [],
      icon: {
        stateIcon: this.$icons.stateIcon
      }
    };
  },
  props: {
    host_id: Number,
    module_type:Number
  },
  created() {
    this.__getDropDownDataCustom();
  },
  methods: {
    reload() {
      this.$refs.hostservice_ips_custom_strategy_unit_table.reload();
    },
    search() {
      this.params.page = 1
      this.reload()
    },
    selectChange(select) {
      this.selectData = select;
    },
    add() {
      this.$layer.iframe({
        content: {
          content: customConfigAdd,
          parent: this,
          data: {
            host_id: this.host_id,
            dropDownData: this.dropDownData,
            module_type:this.module_type
          }
        },
        area: ["500px", "auto"],
        title: "添加自定义规则",
        shadeClose: false
      });
    },
    del(item) {
      let ids = "";
      if (Array.isArray(item)) {
        if (this.$tools.isArrayNull(item)) {
          this.$message({
            type: "error",
            message: "请选择需要删除的选项"
          });
          return;
        }
        ids = this.$tools.mergeId(item);
      } else {
        ids = item.id.toString();
      }
      this.$isShowTip(
        "hostservice",
        "hostservice_ips",
        "确定删除吗",
        () => {
          this.__delRuleCustom({ id: ids,host_id:this.host_id });
        },
        this
      );
    },
    clearAll() {
      this.$isShowTip(
        "hostservice",
        "hostservice_ips",
        "确定清空所有吗",
        () => {
          this.__clearRuleCustom({ clear: 1,host_id:this.host_id });
        },
        this
      );
    },
    openUsing(item) {
      this.$isShowTip(
        "hostservice",
        "hostservice_ips",
        "确定启用吗",
        () => {
          this.__openUsedCustom({
            host_id: this.host_id,
            id: item.id,
            status: 1
          });
        },
        this
      );
    },
    stopUsing(item) {
      this.$isShowTip(
        "hostservice",
        "hostservice_ips",
        "确定停用吗",
        () => {
          this.__stopUsedCustom({
            host_id: this.host_id,
            id: item.id,
            status: 0
          });
        },
        this
      );
    },
    __openUsedCustom(param) {
      openUsedCustom(param)
        .then(res => {
          this.$message({
            type: "success",
            message: "启用成功"
          });
          this.reload();
        })
        .catch(err => {});
    },
    __stopUsedCustom(param) {
      stopUsedCustom(param)
        .then(res => {
          this.$message({
            type: "success",
            message: "停用成功"
          });
          this.reload();
        })
        .catch(err => {});
    },
    __delRuleCustom(param) {
      delRuleCustom(param)
        .then(res => {
          this.$message({
            type: "success",
            message: "删除成功"
          });
          this.reload();
        })
        .catch(err => {});
    },
    __clearRuleCustom(param) {
      clearRuleCustom(param)
        .then(res => {
          this.$message({
            type: "success",
            message: "清除成功"
          });
          this.reload();
        })
        .catch(err => {});
    },
    __getDropDownDataCustom(param) {
      getDropDownDataCustom(param)
        .then(res => {
          let arr = [];
          let data = res.data;
          Object.keys(data).forEach(key => {
            let map = {};
            map.id = key;
            map.name = data[key];
            arr.push(map);
          });
          this.dropDownData.type = arr;
        })
        .catch(err => {});
    }
  }
};
</script>
<style lang="less" scoped>
.hostservice-ips-custom_strategy_unit {
  width: 100%;
  padding: 20px;
}
</style>


