<template>
  <div class="hostservice-ips-office_strategy_unit">
    <g-el-table
      :tableHeadShow="tableHeadShow"
      :url="url"
      :type="type"
      :cloumns="cloumns"
      :params="params"
      :pageParams="pageParams"
      @selectChange="selectChange"
      ref="hostservice_ips_office_strategy_unit_table"
    >
      <template v-slot:table_header="props">
        <el-button type="primary" size="mini" @click="reload">刷新</el-button>
      </template>
      <!-- 操作 -->
      <template v-slot:operation="props">
        <el-button type="primary" size="mini" @click="detail(props.data.row)">详情</el-button>
        <el-button
          type="primary"
          size="mini"
          v-show="props.data.row.status == 0"
          @click="openUsing(props.data.row)"
        >启用</el-button>
        <el-button
          type="danger"
          size="mini"
          v-show="props.data.row.status == 1"
          @click="stopUsing(props.data.row)"
        >停用</el-button>
      </template>
      <!-- 分类名称 -->
      <template v-slot:name_head="props">
        <el-input
          v-model="params.name"
          size="mini"
          placeholder="请输入内容"
          @keyup.enter.native="search"
          :maxLength="20"
        ></el-input>
      </template>
      <!-- 说明 -->
      <template v-slot:description_head="props">
        <el-input
          v-model="params.description"
          size="mini"
          placeholder="请输入内容"
          @keyup.enter.native="search"
          :maxLength="20"
        ></el-input>
      </template>
      <!-- 总量/启用 -->
      <template
        slot="totalAndUsed"
        slot-scope="props"
      >{{props.data.row.total}}/{{props.data.row.used}}</template>
      <!-- 是否启用 -->
      <template slot="status" slot-scope="props">
        <div v-if="props.data.row.status == 1">
          <span v-html="icon.stateIcon.success"></span>启用
        </div>
        <div v-else-if="props.data.row.status == 0">
          <span v-html="icon.stateIcon.error"></span>未启用
        </div>
      </template>
      <template v-slot:status_head="props">
        <el-select v-model="params.status" clearable @change="search" size="mini" placeholder="请选择">
          <el-option
            v-for="item in dropDownData.status"
            :key="item.id"
            :label="item.name"
            :value="item.id"
          ></el-option>
        </el-select>
      </template>
    </g-el-table>
  </div>
</template>
<script>
import configDetail from "./config_detail";
import { stateIcon } from "common/js/tagIcon";
import {
  openUsedOfficeGroup,
  stopUsedOfficeGroup
} from "req/api/hostservice/ips";
export default {
  data() {
    return {
      url: `/normalrule/group/${this.host_id}/list`,
      type: "get",
      cloumns: [
        {
          type: "selection",
          fixed: "left"
        },
        {
          type: "index",
          // fixed: "left",
          label: "序号",
          second_head: {
            type: "index",
            width: "60px",
            label: " ",
            indexMethod: function(index) {
              return index + 1;
            }
          }
        },
        {
          label: "分类名称",
          sortable: true,
          prop: "name",
          second_head: {
            width: "150px",
            prop: "name",
            templateHeadName: "name_head"
          }
        },
        {
          label: "说明",
          sortable: true,
          prop: "description",
          second_head: {
            minWidth:"200px",
            prop: "description",
            templateHeadName: "description_head"
          }
        },
        {
          label: "是否启用",
          prop: "status",
          sortable: true,
          second_head: {
            isTemplate: true,
            templateName: "status",
            width: "150px",
            templateHeadName: "status_head",
            prop: "status"
          }
        },
        {
          label: "总量/启用",
          sortable: true,
          second_head: {
            label: "",
            width: "120px",
            prop: "totalAndUsed",
             isTemplate: true,
            templateName: "totalAndUsed"
          }
        },
        {
          label: "操作",
          second_head: {
            label: "",
            width: "180px",
            isTemplate: true,
            templateName: "operation"
          }
        }
      ],
      tableHeadShow: true,
      params: {
        page: 1,
        limit: 10,
        name: "",
        description: "",
        status: "",
        total: ""
      },
      pageParams: {
        layout: "total, sizes, prev, pager, next, jumper",
        pageAlign: "left",
        pageSizes: [10, 20, 30]
      },
      dropDownData: {
        status: [
          {
            id: 1,
            name: "启用"
          },
          {
            id: 0,
            name: "未启用"
          }
        ]
      },
      selectData: [],
      icon: {
        stateIcon: stateIcon
      }
    };
  },
  props: {
    host_id: {
      type: Number
    },
    module_type:Number
  },
  methods: {
    reload() {
      this.$refs.hostservice_ips_office_strategy_unit_table.reload();
    },
    search() {
      this.params.page = 1
      this.reload()
    },
    selectChange(select) {
      this.selectData = select;
    },
    openUsing(item) {
      this.$isShowTip(
        "hostservice",
        "hostservice_ips",
        "确定启用吗",
        () => {
          this.__openUsedOfficeGroup({
            host_id: this.host_id,
            id:item.id,
            status:1
          });
        },
        this
      );
    },
    stopUsing(item) {
      this.$isShowTip(
        "hostservice",
        "hostservice_ips",
        "确定停用吗",
        () => {
          this.__stopUsedOfficeGroup({
            host_id: this.host_id,
            id:item.id,
            status:0
          });
        },
        this
      );
    },
    detail(item) {
      this.$layer.iframe({
        content: {
          content: configDetail,
          parent: this,
          data: {
            host_id: this.host_id,
            group_id: item.id,
            module_type:this.module_type
          }
        },
        area: ["850px", "auto"],
        title: "规则配置详情",
        shadeClose: false
      });
    },
    __openUsedOfficeGroup(param) {
      openUsedOfficeGroup(param)
        .then(res => {
          this.$message({
            type: "success",
            message: "启用成功"
          });
          this.reload();
        })
        .catch(err => {});
    },
    __stopUsedOfficeGroup(param) {
      stopUsedOfficeGroup(param)
        .then(res => {
          this.$message({
            type: "success",
            message: "停用成功"
          });
          this.reload();
        })
        .catch(err => {});
    }
  }
};
</script>
<style lang="less" scoped>
.hostservice-ips-office_strategy_unit {
  width: 100%;
  padding: 20px;
}
</style>


