<template>
  <div class="hostservice-ips">
    <g-el-table
    :tableHeadShow="tableHeadShow"
      :url="url"
      :type="type"
      :cloumns="cloumns"
      :params="params"
      :pageParams="pageParams"
      @selectChange="selectChange"
      ref="hostservice_ips_table"
    >
     <template v-slot:table_header>
       <el-button type="primary" size="mini" @click="reload()">刷新</el-button>
        <el-button type="danger" size="mini" @click="removeModule(selectData)">移除主机</el-button>
        <el-button type="primary" size="mini" @click="addModule()">添加主机</el-button>
       
      </template>
      <!-- 操作 -->
      <template v-slot:operation="props">
        <el-button type="primary" size="mini" :disabled="props.data.row.online == 0" @click="officeStrategyUnit(props.data.row)">官方策略组</el-button>
        <el-button type="primary" size="mini" :disabled="props.data.row.online == 0" @click="customStrategyUnit(props.data.row)">自定义策略</el-button>
        <el-button type="primary" size="mini" :disabled="props.data.row.online == 0" @click="showNetLog(props.data.row)">实时命中告警</el-button>
        <el-button type="primary" size="mini" :disabled="props.data.row.online == 0" @click="setConfig(props.data.row)">功能设置</el-button>
        <el-button type="primary" size="mini" :disabled="props.data.row.online == 0 || props.data.row.os != 2" @click="currentRules(props.data.row)">操作系统防火墙规则</el-button>
      </template>
      <!-- 资产名称 -->
      <template v-slot:name_head="props">
        <el-input
          v-model="params.name"
          size="mini"
          placeholder="请输入内容"
          @keyup.enter.native="search"
          :maxLength="20"
        ></el-input>
      </template>
      <!-- IP地址 -->
      <template v-slot:ip_head="props">
        <el-input
          v-model="params.ip"
          size="mini"
          placeholder="请输入内容"
          @keyup.enter.native="search"
          :maxLength="20"
        ></el-input>
      </template>
      <!-- 状态 -->
      <template slot="online" slot-scope="props">
        <div v-if="props.data.row.online == 1">
          <span v-html="icon.linkIcon.link"></span>在线<span v-show="props.data.row.version">[v{{props.data.row.version}}]</span>
        </div>
        <div v-else-if="props.data.row.online == 0">
          <span v-html="icon.linkIcon.unlink"></span>离线<span v-show="props.data.row.version">[v{{props.data.row.version}}]</span>
        </div>
      </template>
      <template v-slot:online_head="props">
        <el-select
          v-model="params.online"
          @change="search"
          clearable
          size="mini"
          placeholder="请选择"
        >
          <el-option
            v-for="item in dropDownData.online"
            :key="item.id"
            :label="item.name"
            :value="item.id"
          ></el-option>
        </el-select>
      </template>
      <!-- 当前模式 -->
      <template slot="type" slot-scope="props">
        <div v-if="props.data.row.hgshield.type == 1">
          IPS
        </div>
        <div v-else-if="props.data.row.hgshield.type == 2">
          IDS
        </div>
      </template>

      <template v-slot:type_head="props">
        <el-select
          v-model="params.type"
          @change="search"
          clearable
          size="mini"
          placeholder="请选择"
        >
          <el-option
            v-for="item in dropDownData.type"
            :key="item.id"
            :label="item.name"
            :value="item.id"
          ></el-option>
        </el-select>
      </template>
      <!-- 系统类别 -->
      <template slot="os" slot-scope="props">
        <svg class="icon_1x5" aria-hidden="true" v-html="dropDownData.os[props.data.row.os].icon" />
        <span>{{dropDownData.os[props.data.row.os].name}}</span>
      </template>
      <template v-slot:os_head="props">
        <el-select v-model="params.os" clearable @change="search" size="mini" placeholder="请选择">
          <el-option
            v-for="item in dropDownData.os"
            :key="item.id"
            :label="item.name"
            :value="item.id"
          ></el-option>
        </el-select>
      </template>
    </g-el-table>
  </div>
</template>
<script>
import officeStrategyUnit from "./office_strategy_unit";
import customStrategyUnit from "./custom_strategy_unit";
import currentRules from "./current_rules"
import setConfig from "./set_config";
import netLog from "./net_log";
import addModule from "views/hostservice/addModule"
import moduleRefresh from "common/mixins/modulRefresh"
import { delModule } from "req/api/hostservice/addModule";
export default {
  data() {
    return {
      url: "/host/list/hgshield",
      type: "post",
      cloumns: [
        {
          type: "selection",
          fixed: "left"
        },
        {
          type: "index",
          // fixed: "left",
          label: "序号",
          second_head: {
            type: "index",
            width: "60px",
            label: " ",
            indexMethod: function(index) {
              return index + 1;
            }
          }
        },
        {
          label: "资产名称",
          sortable: true,
          prop: "name",
          second_head: {
            width:"200px",
            prop: "name",
            templateHeadName: "name_head"
          }
        },
        {
          label: "IP地址",
          sortable: true,
          prop: "ip",
          sortMethod: this.$tools.IPSort,
          second_head: {
            width:"200px",
            prop: "ip",
            templateHeadName: "ip_head"
          }
        },
        {
          label: "系统类别",
          prop: "os",
          sortable: true,
          second_head: {
            isTemplate: true,
            width:"200px",
            templateName: "os",
            templateHeadName: "os_head",
            prop: "os"
          }
        },
        {
          label: "状态",
          prop: "online",
          sortable: true,
          second_head: {
            width: "150px",
            isTemplate: true,
            templateName: "online",
            templateHeadName: "online_head",
            prop: "online"
          }
        },
        {
          label: "当前模式",
          prop: "type",
          sortable: true,
          second_head: {
            width: "150px",
            isTemplate: true,
            templateName: "type",
            // templateHeadName: "type_head",
            prop: "type"
          }
        },
        {
          label: "操作",
          second_head: {
            label: "",
            minWidth: "650px",
            isTemplate: true,
            templateName: "operation"
          }
        }
      ],
      tableHeadShow: true,
      params: {
        page: 1,
        limit: 10,
        ip:"",
        name: "",
        os: "",
        online:""
      },
      pageParams: {
        layout: "total, sizes, prev, pager, next, jumper",
        pageAlign: "left",
        pageSizes: [10, 20, 30]
      },
      dropDownData: {
        online: [
          {
            id: 1,
            name: "在线"
          },
          {
            id: 0,
            name: "离线"
          }
        ],
        type: [
          {
            id: 1,
            name: "IPS"
          },
          {
            id: 2,
            name: "IDS"
          }
        ],
        os: this.$icons.osIcon
      },
      selectData: [],
      icon: {
        linkIcon: this.$icons.linkIcon
      },
    };
  },
  mixins: [moduleRefresh("hostservice_ips_table")],
  methods: {
    search() {
      this.params.page = 1
      this.reload()
    },
    selectChange(select) {
      this.selectData = select;
    },
    addModule() {
      this.$layer.iframe({
        content: {
          content: addModule,
          parent: this,
          data: {
            moduleType:"hgshield",
            tipText:"hostservice_ips"
          }
        },
        area: ["700px", "auto"],
        title: "添加主机 ( 列表中将自动过滤与本模块冲突的资产 )",
        shadeClose: false
      });
    },
    removeModule(item) {
      let ids = "";
      if (Array.isArray(item)) {
        if (this.$tools.isArrayNull(item)) {
          this.$message({
            type: "error",
            message: "请选择需要移除的主机!"
          });
          return;
        }
        ids = this.$tools.mergeId(item);
      } else {
        ids = item.id;
      }
      this.$isShowTip(
        "hostservice",
        "hostservice_ips",
        "确定移除吗?",
        () => {
          this.__delModule({ host_id: ids, module: "hgshield" });
        },
        this
      );
    },
    __delModule(param) {
      delModule(param)
        .then(res => {
          this.$message({
            type: "success",
            message: "移除成功"
          });
          this.reload();
        })
        .catch(err => {});
    },
    setConfig(item) {
      this.$layer.iframe({
        content: {
          content: setConfig,
          parent: this,
          data: {
            host_id: item.id,
            host_os:item.os,
            hostNameList:item.network_name
          }
        },
        area: ["500px", "auto"],
        title: "功能设置",
        shadeClose: false
      });
    },
    customStrategyUnit(item) {
      this.$layer.iframe({
        content: {
          content: customStrategyUnit,
          parent: this,
          data: {
            host_id: item.id,
            module_type:item.hgshield ? item.hgshield.type : 0
          }
        },
        area: ["1200px", "auto"],
        title: "自定义策略组",
        shadeClose: false
      });
    },
    officeStrategyUnit(item) {
      this.$layer.iframe({
        content: {
          content: officeStrategyUnit,
          parent: this,
          data: {
            host_id: item.id,
            module_type:item.hgshield ? item.hgshield.type : 0
          }
        },
        area: ["1000px", "auto"],
        title: "官方策略组",
        shadeClose: false
      });
    },
    showNetLog(item) {
      this.$layer.iframe({
        content: {
          content: netLog,
          parent: this,
          data: {
            hostId: item.id
          }
        },
        area: ["1850px", "auto"],
        title: "实时命中告警",
        shadeClose: false
      });
    },
    currentRules(item) {
      this.$layer.iframe({
        content: {
          content: currentRules,
          parent: this,
          data: {
            host_id: item.id
          }
        },
        area: ["850px", "auto"],
        title: "操作系统防火墙规则",
        shadeClose: false
      });
    }
  }
};
</script>


