import { ajaxPro, reqUnlodingPro } from "req/http/http";

export function openUsedOfficeGroup(param) {
  return ajaxPro({
    url: `normalrule/group/${param.host_id}/open`,
    type: "post",
    data: param
  })
    .then(res => {
      return Promise.resolve(res);
    })
    .catch(err => {
      return Promise.reject(err);
    });
}

export function stopUsedOfficeGroup(param) {
  return ajaxPro({
    url: `normalrule/group/${param.host_id}/close`,
    type: "post",
    data: param
  })
    .then(res => {
      return Promise.resolve(res);
    })
    .catch(err => {
      return Promise.reject(err);
    });
}

