<template>
  <div class="hostservice-ips-current_rules">
    <g-el-table
      :url="url"
      :type="type"
      :cloumns="cloumns"
      :tableHeadShow="tableHeadShow"
      :params="params"
      :pageParams="pageParams"
      ref="hostservice_ips_current_rules_table"
    >
      <template v-slot:table_header>
        <el-button type="primary" size="mini" @click="changeRuleUpdate()">转换客户端规则并上传</el-button>
      </template>
      <!-- 操作 -->
      <template slot="oparention" slot-scope="props">
        <el-button type="primary" size="mini" v-if="props.data.row.status == 0" @click="openControl(props.data.row)">开启监控</el-button>
        <el-button type="danger" size="mini" v-else @click="closeControl(props.data.row)">关闭监控</el-button>
        <el-button type="danger" size="mini" @click="del(props.data.row)">删除</el-button>
      </template>
    </g-el-table>
  </div>
</template>
<script>
import {
  changeRuleUp,
  delRule,
  openMonitorRule,
  closeMonitorRule
} from "req/api/hostservice/ips";
export default {
  data() {
    return {
      url: `ips/originalrlue/${this.host_id}/list`,
      type: "post",
      tableHeadShow: true,
      cloumns: [
        {
          type: "index",
          fixed: "left",
          label: "序号",
          width: "60px",
          indexMethod: function(index) {
            return index + 1;
          }
        },
        {
          label: "规则",
          sortable: true,
          prop: "rule"
        },
        {
          label: "操作",
          sortable: true,
          width: "200px",
          prop: "oparention",
          isTemplate: true,
          templateName: "oparention"
        }
      ],
      params: {
        page: 1,
        limit: 10,
        rule: ""
      },
      pageParams: {
        layout: "total, sizes, prev, pager, next, jumper",
        pageAlign: "left",
        pageSizes: [10, 20, 30]
      }
    };
  },
  props: {
    host_id: Number
  },
  methods: {
    reload() {
      this.$refs.hostservice_ips_current_rules_table.reload();
    },
    changeRuleUpdate() {
      this.$isShowTip(
        "hostservice",
        "hostservice_ips",
        "转换并上传客户端防火墙规则后,将接管并锁定客户端防火墙,确定转换并上传吗?",
        () => {
          this.__changeRuleUp({
            host_id: this.host_id
          });
        },
        this
      );
    },
    openControl(item) {
      this.$isShowTip(
        "hostservice",
        "hostservice_ips",
        "确定开启监控吗",
        () => {
          this.__openMonitorRule({
            id: item.id,
            host_id: this.host_id
          });
        },
        this
      );
    },
    closeControl(item) {
      this.$isShowTip(
        "hostservice",
        "hostservice_ips",
        "确定关闭监控吗",
        () => {
          this.__closeMonitorRule({
            id: item.id,
            host_id: this.host_id
          });
        },
        this
      );
    },
    del(item) {
      this.$isShowTip(
        "hostservice",
        "hostservice_ips",
        "确定删除吗",
        () => {
          this.__delRule({
            id: item.id,
            host_id: this.host_id
          });
        },
        this
      );
    },
    __openMonitorRule(param) {
      openMonitorRule(param)
        .then(res => {
          this.$message({
            type: "success",
            message: "开启监控成功"
          });
          this.reload();
        })
        .catch(err => {});
    },
    __closeMonitorRule(param) {
      closeMonitorRule(param)
        .then(res => {
          this.$message({
            type: "success",
            message: "关闭监控成功"
          });
          this.reload();
        })
        .catch(err => {});
    },
    __delRule(param) {
      delRule(param)
        .then(res => {
          this.$message({
            type: "success",
            message: "删除成功"
          });
          this.reload();
        })
        .catch(err => {});
    },
    __changeRuleUp(param) {
      changeRuleUp(param)
        .then(res => {
          this.$message({
            type: "success",
            message: "转换客户端规则并上传成功"
          });
          this.reload();
        })
        .catch(err => {});
    }
  }
};
</script>
<style lang="less">
.borderSolid {
  border-left: 1px solid #ededed;
}
.hostservice-ips-current_rules {
  width: 100%;
  margin: 10px;
  border-right: 1px solid #ededed;
  td {
    &:extend(.borderSolid);
  }
  th {
    &:extend(.borderSolid);
  }
}
</style>


