<template>
  <div class="hostservice-ips_net_log">
    <g-el-table
      :tableHeadShow="tableHeadShow"
      :url="url"
      :type="type"
      :cloumns="cloumns"
      :params="params"
      :pageParams="pageParams"
      @selectChange="selectChange"
      ref="hostservice_ips_net_log_table"
    >
      <template v-slot:table_header="props">
        <el-button type="primary" size="mini" @click="reload">刷新</el-button>
        <el-button
          type="danger"
          size="mini"
          @click="exporthostservice(selectData)"
          >导出</el-button
        >
      </template>
      <!-- 攻击说明 -->
      <template v-slot:description_head="props">
        <el-input
          v-model="params.description"
          size="mini"
          placeholder="请输入内容"
          @keyup.enter.native="search"
          :maxLength="20"
        ></el-input>
      </template>
      <template v-slot:description="props">
        <span :class="props.data.row.ctime | filterIsNew(firstTime)">
          {{ props.data.row.description }}</span
        >
      </template>
      <!-- 攻击域名 -->
      <!-- <template v-slot:domain_name_head="props">
        <el-input
          v-model="params.domain_name"
          size="mini"
          placeholder="请输入内容"
          @keyup.enter.native="search"
          :maxLength="20"
        ></el-input>
      </template> -->
      <!-- 攻击来源 -->
      <template v-slot:attack_src_head="props">
        <el-input
          v-model="params.attack_src"
          :maxLength="20"
          size="mini"
          placeholder="请输入内容"
          @keyup.enter.native="search"
        ></el-input>
      </template>
      <!-- 攻击目标 -->
      <template v-slot:attack_dest_head="props">
        <el-input
          v-model="params.attack_dest"
          :maxLength="20"
          size="mini"
          placeholder="请输入内容"
          @keyup.enter.native="search"
        ></el-input>
      </template>
      <!-- 攻击URL -->
      <!-- <template v-slot:url_head="props">
        <el-input
          v-model="params.url"
          :maxLength="20"
          size="mini"
          placeholder="请输入内容"
          @keyup.enter.native="search"
        ></el-input>
      </template> -->
      <!-- 攻击时间 -->
      <template v-slot:atime_head="props">
        <el-date-picker
          type="date"
          size="mini"
          placeholder="选择日期"
          value-format="yyyy-MM-dd"
          v-model="params.atime"
          @change="search"
          style="width: 100%;"
        ></el-date-picker>
      </template>
      <!-- 攻击类型 -->
      <template v-slot:type_head="props">
        <el-select
          v-model="params.type"
          @change="search"
          clearable
          size="mini"
          placeholder="请选择"
        >
          <el-option
            v-for="item in dropDownData.type"
            :key="item.id"
            :label="item.value"
            :value="item.id"
          ></el-option>
        </el-select>
      </template>
      <!-- 风险级别 -->
      <template slot="level" slot-scope="props">
        <div v-if="props.data.row.level == '极高'">
          <span class="colorPurple">极高</span>
        </div>
        <div v-else-if="props.data.row.level == '高'">
          <span class="colorRed">高</span>
        </div>
        <div v-else-if="props.data.row.level == '中'">
          <span class="colorOrange">中</span>
        </div>
        <div v-else-if="props.data.row.level == '低'">
          <span class="colorBlueness">低</span>
        </div>
      </template>
      <template v-slot:level_head="props">
        <el-select
          v-model="params.level"
          clearable
          @change="search"
          size="mini"
          placeholder="请选择"
        >
          <el-option
            v-for="item in dropDownData.level"
            :key="item.id"
            :label="item.name"
            :value="item.id"
          ></el-option>
        </el-select>
      </template>
      <!-- 处理结果 -->
      <!-- <template slot="handle" slot-scope="props">
        <div v-if="props.data.row.handle == '通过'">
          <span v-html="icon.stateIcon.success"></span>通过
        </div>
        <div v-else-if="props.data.row.handle == '警告'">
          <span v-html="icon.stateIcon.error"></span>警告
        </div>
        <div v-else-if="props.data.row.handle == '拒绝'">
          <span v-html="icon.stateIcon.pending"></span>拒绝
        </div>
      </template>
      <template v-slot:handle_head="props">
        <el-select
          v-model="params.handle"
          clearable
          @change="search"
          size="mini"
          placeholder="请选择"
        >
          <el-option
            v-for="item in dropDownData.handle"
            :key="item.id"
            :label="item.name"
            :value="item.id"
          ></el-option>
        </el-select>
      </template> -->
      <!-- 数据包 -->
      <!-- <template slot="data_package" slot-scope="props">
        <div class="ips_download_btn">
          <mu-icon
            size="32"
            value="cloud_download"
            color="blue"
            @click="ipsDown(props.data.row.id)"
          ></mu-icon>
        </div>
      </template> -->
      <!-- 展开 -->
      <template slot="expand" slot-scope="props">
        <el-form label-position="left" inline class="ahsmp-table-expand">
          <el-form-item label="攻击域名:">
            <span>{{ props.data.row.domain_name }}</span>
          </el-form-item>
          <el-form-item label="攻击URL:">
            <span>{{ props.data.row.url }}</span>
          </el-form-item>
          <el-form-item label="处理结果:">
            <div v-if="props.data.row.handle == '通过'">
              <span v-html="icon.stateIcon.success"></span>通过
            </div>
            <div v-else-if="props.data.row.handle == '警告'">
              <span v-html="icon.stateIcon.error"></span>警告
            </div>
            <div v-else-if="props.data.row.handle == '拒绝'">
              <span v-html="icon.stateIcon.pending"></span>拒绝
            </div>
          </el-form-item>
          <el-form-item label="完整数据包:">
            <div class="ips_download_btn">
              <mu-icon
                size="32"
                value="cloud_download"
                color="blue"
                @click="ipsDown(props.data.row.id)"
              ></mu-icon>
            </div>
          </el-form-item>
          <el-form-item label="日志操作:">
            <el-button
              type="primary"
              size="mini"
              v-if="props.data.row.status == 0"
              @click="openConfig(props.data.row)"
              >启用当条规则</el-button
            >
            <el-button
              type="danger"
              size="mini"
              v-else
              @click="closeConfig(props.data.row)"
              >停用当条规则</el-button
            >
            <el-button
              type="primary"
              size="mini"
              v-if="props.data.row.allow_ip_rule_id == 0"
              @click="WhiteList(props.data.row)"
              >设置IP白名单</el-button
            >
            <el-button
              type="danger"
              size="mini"
              v-else
              @click="colseWhiteList(props.data.row)"
              >取消IP白名单</el-button
            >
          </el-form-item>
        </el-form>
      </template>
      <!-- 操作 -->
      <!-- <template slot="operation" slot-scope="props">
        <el-button
          type="primary"
          size="mini"
          v-if="props.data.row.allow_ip_rule_id == 0"
          @click="WhiteList(props.data.row)"
          >设置为白名单</el-button
        >
        <el-button type="primary" size="mini" v-else disabled
          >设置为白名单</el-button
        >
        <el-button
          type="primary"
          size="mini"
          :disabled="props.data.row.status == 0"
          @click="closeConfig(props.data.row)"
          >停用规则</el-button
        >
      </template> -->
    </g-el-table>
  </div>
</template>
<script>
import {
  setWhiteList,
  cancelWhiteList,
  getDropDownDataLog,
  downLoadDataBag,
  closeConfigLog,
  openConfigLog
} from "req/api/hostservice/ips";
import warningNew from "common/mixins/warningNew";
export default {
  data() {
    return {
      url: `/networkattack/${this.hostId}/list`,
      type: "get",
      cloumns: [
        {
          type: "selection",
          fixed: "left"
        },
        {
          type: "index",
          // fixed: "left",
          label: "序号",
          second_head: {
            type: "index",
            width: "60px",
            label: " ",
            indexMethod: function(index) {
              return index + 1;
            }
          }
        },
        {
          label: "攻击说明",
          sortable: true,
          prop: "description",
          second_head: {
            minWidth: "200px",
            prop: "description",
            templateName:"description",
            isTemplate:true,
            templateHeadName: "description_head"
          }
        },
        {
          label: "攻击来源",
          sortable: true,
          prop: "attack_src",
          second_head: {
            width: "300px",
            prop: "attack_src",
            templateHeadName: "attack_src_head"
          }
        },
        {
          label: "攻击目标",
          sortable: true,
          second_head: {
            label: "",
            prop: "attack_dest",
            width: "300px",
            templateHeadName: "attack_dest_head"
          }
        },
        {
          label: "攻击时间",
          sortable: true,
          prop: "atime",
          second_head: {
            width: "160px",
            prop: "atime",
            templateHeadName: "atime_head"
          }
        },
        {
          label: "攻击类型",
          sortable: true,
          prop: "type",
          second_head: {
            width: "150px",
            prop: "type",
            templateHeadName: "type_head"
          }
        },
        {
          label: "风险级别",
          prop: "level",
          sortable: true,
          second_head: {
            width: "110px",
            isTemplate: true,
            templateName: "level",
            templateHeadName: "level_head",
            prop: "level"
          }
        },
        {
          type: "expand",
          fixed: "right",
          width: "60px",
          isTemplate: true,
          templateName: "expand"
        }
        // {
        //   label: "攻击域名",
        //   sortable: true,
        //   prop: "domain_name",
        //   sortMethod: this.$tools.domain_nameSort,
        //   second_head: {
        //     width: "150px",
        //     prop: "domain_name",
        //     templateHeadName: "domain_name_head"
        //   }
        // },
        // {
        //   label: "攻击URL",
        //   sortable: true,
        //   prop: "url",
        //   second_head: {
        //     width: "150px",
        //     prop: "url",
        //     templateHeadName: "url_head"
        //   }
        // },
        // {
        //   label: "处理结果",
        //   prop: "handle",
        //   sortable: true,
        //   second_head: {
        //     width: "110px",
        //     isTemplate: true,
        //     templateName: "handle",
        //     templateHeadName: "handle_head",
        //     prop: "handle"
        //   }
        // },
        // {
        //   label: "完整数据包",
        //   prop: "data_package",
        //   second_head: {
        //     label: "",
        //     align: "center",
        //     width: "110px",
        //     isTemplate: true,
        //     templateName: "data_package"
        //   }
        // },
        // {
        //   label: "操作",
        //   second_head: {
        //     label: "",
        //     width: "230px",
        //     isTemplate: true,
        //     templateName: "operation"
        //   }
        // },
      ],
      tableHeadShow: true,
      params: {
        page: 1,
        limit: 10,
        description: "",
        type: "",
        attack_src: "",
        attack_dest: "",
        domain_name: "",
        url: "",
        atime: "",
        level: "",
        handle: ""
      },
      pageParams: {
        layout: "total, sizes, prev, pager, next, jumper",
        pageAlign: "left",
        pageSizes: [10, 20, 30]
      },
      dropDownData: {
        handle: [
          {
            id: 0,
            name: "拒绝"
          },
          {
            id: 1,
            name: "警告"
          },
          {
            id: 32,
            name: "通过"
          }
        ],
        level: [
          {
            id: 4,
            name: "低"
          },
          {
            id: 3,
            name: "中"
          },
          {
            id: 2,
            name: "高"
          },
          {
            id: 1,
            name: "极高"
          }
        ],
        type: []
      },
      icon: {
        stateIcon: this.$icons.stateIcon
      },
      selectData: []
    };
  },
  props: {
    hostId: Number
  },
  mixins: [warningNew("hostservice_ips_net_log_table")],
  created() {
    this.__getDropDownDataLog();
  },
  methods: {
    reload() {
      this.$refs.hostservice_ips_net_log_table.reload();
    },
    search() {
      this.params.page = 1;
      this.reload();
    },
    selectChange(select) {
      this.selectData = select;
    },
    closeConfig(item) {
      this.$isShowTip(
        "hostservice",
        "hostservice_ips",
        "确定停用当条规则吗?",
        () => {
          this.__closeConfigLog({
            id: item.id,
            host_id: this.hostId
          });
        },
        this
      );
    },
    openConfig(item) {
      this.$isShowTip(
        "hostservice",
        "hostservice_ips",
        "确定启用当条规则吗?",
        () => {
          this.__openConfigLog({
            id: item.id,
            host_id: this.hostId
          });
        },
        this
      );
    },
    exporthostservice(array) {
      if (this.$tools.isArrayNull(array)) {
        this.$message({
          type: "error",
          message: "请选择要导出的日志选项!"
        });
        return;
      }
      let str =
        "序号,攻击说明,攻击来源,攻击目标,攻击域名,攻击URL,攻击时间,攻击类型,风险级别,处理结果\n";
      var inum = 0;
      for (let item of array) {
        inum++;
        let description = item.description;
        description = description.replace(",", "，");
        str += `${inum},${description},${item.attack_src},${item.attack_dest},${item.domain_name},${item.url},${item.atime},${item.type},${item.level},${item.handle},\n`;
      }
      this.$tools.exportSCV(str, `网络日志`);
    },
    ipsDown(id) {
      this.__downLoadDataBag({
        id: id
      });
    },
    colseWhiteList(item) {
      this.$isShowTip(
        "hostservice",
        "hostservice_ips",
        "确定取消为IP白名单吗?",
        () => {
          this.__cancelWhiteList({
            id: item.id,
            rule_id:item.rule_id,
            host_id: this.hostId,
            src_ip: item.src_ip,
            event_id: item.event_id,
            atime: item.atime
          });
        },
        this
      );
    },
    WhiteList(item) {
      this.$isShowTip(
        "hostservice",
        "hostservice_ips",
        "确定设置为IP白名单吗?",
        () => {
          this.__setWhiteList({
            id: item.id,
            host_id: this.hostId,
            src_ip: item.src_ip,
            event_id: item.event_id,
            atime: item.atime
          });
        },
        this
      );
    },
    __openConfigLog(param) {
      openConfigLog(param)
        .then(res => {
          this.$message({
            type: "success",
            message: "启用成功"
          });
          this.reload();
        })
        .catch(err => {});
    },
    __closeConfigLog(param) {
      closeConfigLog(param)
        .then(res => {
          this.$message({
            type: "success",
            message: "停用成功"
          });
          this.reload();
        })
        .catch(err => {});
    },
    __setWhiteList(param) {
      setWhiteList(param)
        .then(res => {
          this.$message({
            type: "success",
            message: "设置成功"
          });
          this.reload();
        })
        .catch(error => {});
    },
    __cancelWhiteList(param) {
      cancelWhiteList(param)
        .then(res => {
          this.$message({
            type: "success",
            message: "取消成功"
          });
          this.reload();
        })
        .catch(error => {});
    },
    __downLoadDataBag(param) {
      downLoadDataBag(param)
        .then(res => {
          let path = `http://${res.data.ip}:${res.data.port}/${res.data.path}`;
          this.$tools.downLoad(path);
        })
        .catch(err => {});
    },
    __getDropDownDataLog() {
      getDropDownDataLog()
        .then(res => {
          this.dropDownData.type = res.data.type;
        })
        .catch(err => {});
    }
  }
};
</script>
<style lang="less" scoped>
.hostservice-ips_net_log {
  width: 100%;
  padding: 20px;
  .ips_download_btn {
    cursor: pointer;
  }
}
.ahsmp-table-expand {
  font-size: 0;
}
.ahsmp-table-expand .el-form-item__label {
  width: 110px;
  color: #7a91a9;
  text-align: justify;
  text-align-last: justify;
}
.hostservice-netfilter .el-table__expanded-cell {
  background-color: #f5f7fa;
}
.hostservice-netfilter .el-table__expanded-cell:hover {
  background-color: #f5f7fa !important;
}
.ahsmp-table-expand .el-form-item__label:before {
  content: "";
  display: inline-block;
  width: 4px;
  height: 4px;
  margin-right: 5px;
  background: #7a91a9;
  vertical-align: middle;
}
.ahsmp-table-expand .el-form-item__label:after {
  content: "";
  width: 100%;
  height: 0;
}
.ahsmp-table-expand .el-form-item {
  width: 50%;
  margin-right: 0;
  margin-bottom: 0;
  line-height: 1;
}
.ahsmp-table-expand .el-form-item__content {
  color: #999;
}
.ahsmp-table-expand .el-form-item__content svg {
  vertical-align: middle;
}
</style>



