<template>
  <div class="hostservice-ips-config_detail">
    <g-el-table
      :tableHeadShow="tableHeadShow"
      :url="url"
      :type="type"
      :cloumns="cloumns"
      :params="params"
      :pageParams="pageParams"
      @selectChange="selectChange"
      ref="hostservice_ips_config_detail_table"
    >
      <template v-slot:table_header>
        <el-button
          type="danger"
          size="mini"
          @click="stopUsing(selectData)"
        >批量停用</el-button>
        <el-button
          type="primary"
          size="mini"
          @click="openUsing(selectData)"
        >批量启用</el-button>
        <el-button type="primary" size="mini" @click="search">刷新</el-button>
      </template>
      <!-- 操作 -->
      <template v-slot:operation="props">
        <el-button v-if="module_type != 2" type="primary" size="mini" @click="setConfig(props.data.row)">配置</el-button>
        <el-button
          type="danger"
          v-show="props.data.row.status == 1"
          size="mini"
          @click="stopUsing(props.data.row)"
        >停用</el-button>
        <el-button
          type="primary"
          v-show="props.data.row.status == 0"
          size="mini"
          @click="openUsing(props.data.row)"
        >启用</el-button>
      </template>
      <!-- 说明 -->
      <template v-slot:description_head="props">
        <el-input
          v-model="params.description"
          size="mini"
          placeholder="请输入内容"
          @keyup.enter.native="search"
          :maxLength="20"
        ></el-input>
      </template>
      <!-- 处理方式 -->
      <template v-slot:handle="props">
        <div v-if="module_type == 2">
          <span v-html="icon.stateIcon.error"></span>警告
        </div>
        <div v-else>
          <div v-if="props.data.row.handle == 2">
            <span v-html="icon.stateIcon.error"></span>警告
          </div>
          <div v-else-if="props.data.row.handle == 1">
            <span v-html="icon.stateIcon.success"></span>允许
          </div>
          <div v-else-if="props.data.row.handle == 0">
            <span v-html="icon.stateIcon.pending"></span>阻止
          </div>
        </div>
      </template>
      <template v-slot:handle_head="props" >
        <el-select v-if="module_type != 2" v-model="params.handle" clearable @change="search" size="mini" placeholder="请选择">
          <el-option
            v-for="item in dropDownData.handle"
            :key="item.id"
            :label="item.name"
            :value="item.id"
          ></el-option>
        </el-select>
      </template>
      <!-- 是否启用 -->
      <template slot="status" slot-scope="props">
        <div v-if="props.data.row.status == 1">
          <span v-html="icon.stateIcon.success"></span>启用
        </div>
        <div v-else-if="props.data.row.status == 0">
          <span v-html="icon.stateIcon.error"></span>未启用
        </div>
      </template>
      <template v-slot:status_head="props">
        <el-select v-model="params.status" clearable @change="search" size="mini" placeholder="请选择">
          <el-option
            v-for="item in dropDownData.status"
            :key="item.id"
            :label="item.name"
            :value="item.id"
          ></el-option>
        </el-select>
      </template>
    </g-el-table>
  </div>
</template>
<script>
import configHandle from "./config_handle";
import { openUsedOffice, stopUsedOffice } from "req/api/hostservice/ips";

export default {
  data() {
    return {
      url: `normalrule/${this.host_id}/list/${this.group_id}`,
      type: "get",
      cloumns: [
        {
          type: "selection",
          fixed: "left"
        },
        {
          type: "index",
          // fixed: "left",
          label: "序号",
          second_head: {
            type: "index",
            width: "60px",
            label: " ",
            indexMethod: function(index) {
              return index + 1;
            }
          }
        },
        {
          label: "说明",
          sortable: true,
          prop: "description",
          second_head: {
            minWidth:"200px",
            prop: "description",
            templateHeadName: "description_head"
          }
        },
        {
          label: "处理方式",
          sortable: true,
          prop: "handle",
          second_head: {
            isTemplate: true,
            templateName: "handle",
            width: "130px",
            prop: "handle",
            templateHeadName: "handle_head"
          }
        },
        {
          label: "是否启用",
          prop: "status",
          sortable: true,
          second_head: {
            isTemplate: true,
            templateName: "status",
            width: "130px",
            templateHeadName: "status_head",
            prop: "status"
          }
        },
        {
          label: "操作",
          second_head: {
            label: "",
            width: "180px",
            isTemplate: true,
            templateName: "operation"
          }
        }
      ],
      tableHeadShow: true,
      params: {
        page: 1,
        limit: 10,
        handle: "",
        description: "",
        status: ""
      },
      pageParams: {
        layout: "total, sizes, prev, pager, next, jumper",
        pageAlign: "left",
        pageSizes: [10, 20, 30]
      },
      dropDownData: {
        status: [
          {
            id: 1,
            name: "启用"
          },
          {
            id: 0,
            name: "未启用"
          }
        ],
        handle: [
          {
            id: 0,
            name: "阻止"
          },
          {
            id: 1,
            name: "允许"
          },
          {
            id: 2,
            name: "警告"
          }
        ]
      },
      selectData: [],
      icon: {
        stateIcon: this.$icons.stateIcon
      }
    };
  },
  props: {
    host_id: {
      type: Number
    },
    group_id: {
      type: Number
    },
    module_type:Number
  },
  methods: {
    reload() {
      this.$refs.hostservice_ips_config_detail_table.reload();
    },
    search() {
      this.params.page = 1
      this.reload()
    },
    reloadAll() {
      this.$refs.hostservice_ips_config_detail_table.reload();
      this.$parent.$refs.hostservice_ips_office_strategy_unit_table.reload();
    },
    selectChange(select) {
      this.selectData = select;
    },
    openUsing(item) {
      let ids = "";
      if (Array.isArray(item)) {
        if (this.$tools.isArrayNull(item)) {
          this.$message({
            type: "error",
            message: "请选择批量启用的主机!"
          });
          return;
        }
        ids = this.$tools.mergeId(item);
      } else {
        ids = item.id;
      }
      this.$isShowTip(
        "hostservice",
        "hostservice_ips",
        "确定启用吗",
        () => {
          this.__openUsedOffice({
            host_id: this.host_id,
            id: ids,
            status: 1
          });
        },
        this
      );
    },
    stopUsing(item) {
      let ids = "";
      if (Array.isArray(item)) {
        if (this.$tools.isArrayNull(item)) {
          this.$message({
            type: "error",
            message: "请选择批量停用的主机!"
          });
          return;
        }
        ids = this.$tools.mergeId(item);
      } else {
        ids = item.id;
      }
      this.$isShowTip(
        "hostservice",
        "hostservice_ips",
        "确定停用吗",
        () => {
          this.__stopUsedOffice({
            host_id: this.host_id,
            id: ids,
            status: 0
          });
        },
        this
      );
    },
    setConfig(item) {
      this.$layer.iframe({
        content: {
          content: configHandle,
          parent: this,
          data: {
            layer_data: item,
            host_id: this.host_id,
            dropDownData: this.dropDownData
          }
        },
        area: ["500px", "auto"],
        title: "配置",
        shadeClose: false
      });
    },
    __openUsedOffice(param) {
      openUsedOffice(param)
        .then(res => {
          this.$message({
            type: "success",
            message: "启用成功"
          });
          this.reloadAll();
        })
        .catch(err => {});
    },
    __stopUsedOffice(param) {
      stopUsedOffice(param)
        .then(res => {
          this.$message({
            type: "success",
            message: "停用成功"
          });
          this.reloadAll();
        })
        .catch(err => {});
    }
  }
};
</script>
<style lang="less" scoped>
.hostservice-ips-config_detail {
  width: 100%;
  padding: 20px;
}
</style>


